@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Source+Serif+4:wght@300;400;600&display=swap");
html, body {
  min-height: 100%;
}

html {
  font-size: 62.25%;
}

body {
  line-height: 1.5;
}

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

#root, #__next {
  isolation: isolate;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

:root {
  --color-red: #E84A4A;
  --color-red--rgb: 232, 74, 74;
  --color-pink: #E84A72;
  --color-pink--rgb: 232, 74, 114;
  --color-white: #fdf5f5;
  --color-brown: #EAE2E2;
  --color-green: #4ae894;
  --color-primary: var(--color-red);
  --color-secondary: var(--color-brown);
  --main-gradient: linear-gradient(90deg, var(--color-pink) 0%, var(--color-primary) 100%);
  --main-gradient-hover: linear-gradient(90deg, var(--color-pink) 99%, var(--color-primary) 100%);
  --font-sansserif: "Wotfard";
  --font-serif: "Source Serif 4";
  --font-primary: var(--font-sansserif);
  --font-secondary: var(--font-serif);
  --p-font-size: clamp(1.8rem, calc(.3rem + 1vw), 2.8rem);
  --h1-font-size: clamp(3.2rem, calc(1rem + 4vw), 13rem);
  --h2-font-size: clamp(2.5rem, calc(1rem + 2.5vw), 7rem);
  --h3-font-size: clamp(2.1rem, calc(1rem + 1vw), 3rem);
  --margin-fluid: clamp(4rem, calc(1rem + 7vh), 10rem);
  --margin-fluid--tiny: clamp(2rem, calc(1rem + 4vh), 7rem);
  --padding-global: calc(max(3vw, 2rem));
  --radius-primary: 1rem;
  --radius-secondary: .6rem;
  --border: 2px solid var(--color-primary);
  --gap: clamp(3rem, 2vw, 5rem);
}

@font-face {
  font-family: "Wotfard";
  src: url("../assets/fonts/wotfard-bold-webfont.eot");
  src: url("../assets/fonts/wotfard-bold-webfont.eot?#iefix") format("embedded-opentype"), url("../assets/fonts/wotfard-bold-webfont.woff2") format("woff2"), url("../assets/fonts/wotfard-bold-webfont.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Wotfard";
  src: url("../assets/fonts/wotfard-medium-webfont.eot");
  src: url("../assets/fonts/wotfard-medium-webfont.eot?#iefix") format("embedded-opentype"), url("../assets/fonts/wotfard-medium-webfont.woff2") format("woff2"), url("../assets/fonts/wotfard-medium-webfont.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Wotfard";
  src: url("../assets/fonts/wotfard-regular-webfont.eot");
  src: url("../assets/fonts/wotfard-regular-webfont.eot?#iefix") format("embedded-opentype"), url("../assets/fonts/wotfard-regular-webfont.woff2") format("woff2"), url("../assets/fonts/wotfard-regular-webfont.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Wotfard";
  src: url("../assets/fonts/wotfard-semibold-webfont.eot");
  src: url("../assets/fonts/wotfard-semibold-webfont.eot?#iefix") format("embedded-opentype"), url("../assets/fonts/wotfard-semibold-webfont.woff2") format("woff2"), url("../assets/fonts/wotfard-semibold-webfont.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
}
.grecaptcha-badge {
  display: none;
}

body {
  font-size: var(--p-font-size);
  font-family: var(--font-primary), "Arial", sans-serif;
  font-weight: 400;
  background-color: var(--color-white);
  color: var(--color-primary);
}

.wrapper {
  width: 100%;
  max-width: min(1800px, 90vw);
  padding-left: var(--gap);
  padding-right: var(--gap);
  margin-left: auto;
  margin-right: auto;
}
.wrapper--tiny {
  max-width: max(500px, 75vw);
}
.wrapper p {
  max-width: 90rem;
}
.wrapper p a {
  transition: all ease 0.3s;
  color: inherit;
  font-weight: bolder;
  text-underline-offset: 6px;
}
.wrapper p a:hover {
  text-underline-offset: 10px;
  text-decoration-thickness: 2px;
}

.global-wrapper {
  overflow-x: clip;
  padding-top: var(--padding-global);
  padding-bottom: var(--padding-global);
}

.v-hidden {
  visibility: hidden;
}

.header {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}
.header__nav {
  display: flex;
  gap: 1.5rem;
  align-items: center;
  flex: 1;
  justify-content: right;
  align-self: stretch;
}

.footer {
  background: var(--main-gradient);
  color: var(--color-white);
  margin-top: var(--margin-fluid);
  border-radius: var(--radius-primary);
  overflow-x: hidden;
}
.footer__contact {
  padding-top: 1.5em;
  padding-bottom: 1.5em;
  display: flex;
  align-items: center;
}
.footer__contact__link {
  --beforeWidth: .7rem;
  --beforeMargin: 2rem;
  transition: opacity ease-out 0.2s;
  border: 0;
  background: none;
  font-size: var(--h2-font-size);
  color: var(--color-white);
  font-weight: 300;
  display: flex;
  align-items: center;
  cursor: pointer;
  animation: scroll 20s infinite;
  animation-timing-function: linear;
}
.footer__contact__link:hover {
  opacity: 0.7;
}
.footer__contact__link + .footer__contact__link::before {
  content: "";
  display: block;
  height: var(--beforeWidth);
  width: var(--beforeWidth);
  background-color: var(--color-white);
  border-radius: 1rem;
  margin: var(--beforeMargin);
}
@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(calc((var(--size) + var(--beforeWidth) + var(--beforeMargin) * 2) * -2));
  }
}
.footer__menus {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  padding: 1em 0;
  border-top: 1px solid var(--color-white);
}
@media (width < 650px) {
  .footer__menus__left {
    display: none !important;
  }
}
.footer__menus ul {
  display: contents;
  list-style-type: none;
}
.footer__menus ul:last-child li:last-child {
  flex: 1;
  text-align: right;
}
.footer__menus a {
  transition: opacity ease-out 0.2s;
  color: inherit;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 0.7em;
}
.footer__menus a:hover {
  opacity: 0.7;
}

.logo {
  transition: all ease 0.2s;
  font-weight: 600;
  font-size: 1.2em;
  border: var(--border);
  display: inline-flex;
  border-radius: var(--radius-primary);
  justify-content: center;
  align-items: center;
  color: inherit;
  text-decoration: none;
  height: 5rem;
  min-width: 5rem;
  padding-left: 0.7rem;
  padding-right: 0.7rem;
  text-transform: uppercase;
}
.logo:hover {
  background: var(--main-gradient);
  color: var(--color-white);
}

@media (width > 900px) {
  .burger {
    display: none;
  }
}
@media (width <= 900px) {
  .burger {
    --burgerWidth: 3rem;
    --burgerHeight: 2px;
    --burgerGap: 1rem;
    --color: var(--color-primary);
    --transition: all cubic-bezier(.18,.89,.51,1.2) .3s;
    cursor: pointer;
    border: 0;
    outline: 0;
    background: none;
    display: flex;
    gap: 1rem;
    align-items: center;
    position: relative;
    z-index: 201;
  }
  .burger__title {
    text-transform: uppercase;
    color: var(--color);
    font-weight: 600;
    font-size: 0.9em;
  }
  .burger__bars {
    transition: var(--transition);
    display: block;
    height: var(--burgerHeight);
    width: var(--burgerWidth);
    background-color: var(--color);
    position: relative;
    border-radius: 4px;
  }
  .burger__bars::before, .burger__bars::after {
    transition: all cubic-bezier(0.18, 0.89, 0.41, 1.94) 0.3s;
    content: "";
    display: block;
    height: var(--burgerHeight);
    width: var(--burgerWidth);
    background-color: var(--color);
    position: absolute;
    border-radius: 4px;
  }
  .burger__bars::before {
    top: var(--burgerGap);
  }
  .burger__bars::after {
    bottom: var(--burgerGap);
  }
  .burger[aria-expanded=true] {
    --color: var(--color-white);
  }
  .burger[aria-expanded=true] .burger__bars {
    transform: rotate(45deg);
  }
  .burger[aria-expanded=true] .burger__bars::before {
    transform: rotate(90deg);
    top: 0;
  }
  .burger[aria-expanded=true] .burger__bars::after {
    height: 0;
    opacity: 0;
  }
}
.burger:hover {
  --burgerGap: 1.4rem;
}

.btn a {
  transition: all ease-out 0.4s;
  background-image: linear-gradient(90deg, var(--color-pink) 0%, var(--color-primary) 50%, var(--color-pink) 100%);
  background-size: 200% 100%;
  background-position: left bottom;
  border-radius: var(--radius-secondary);
  color: var(--color-white);
  padding: 0.8em 1.2em;
  text-decoration: none;
  font-weight: 500;
  display: inline-flex;
  box-shadow: 0 0 5rem transparent;
  border: 0;
  cursor: pointer;
}
.btn a:hover {
  background-position: right bottom;
  box-shadow: 0 0 5rem rgba(var(--color-pink--rgb), 0.7);
}

.btn--border a {
  --size: .7rem;
  position: relative;
}
.btn--border a::after {
  content: "";
  position: absolute;
  padding: var(--size);
  border: var(--border);
  border-radius: var(--radius-primary);
  top: calc(var(--size) * -1);
  right: calc(var(--size) * -1);
  bottom: calc(var(--size) * -1);
  left: calc(var(--size) * -1);
}

@media (width > 900px) {
  .header__menu {
    --padding: .4rem;
    list-style-type: none;
    display: flex;
    border: var(--border);
    border-radius: var(--radius-primary);
    height: 100%;
    padding: var(--padding);
    position: relative;
  }
}
@media (width > 900px) and (width <= 900px) {
  .header__menu {
    display: none;
  }
}
@media (width > 900px) {
  .header__menu__wrapper {
    display: contents;
  }
  .header__menu li {
    display: flex;
  }
  .header__menu li:first-child {
    margin-left: 1rem;
  }
  .header__menu li.current_page_item {
    font-weight: 600;
  }
  .header__menu a {
    display: flex;
    height: 100%;
    align-items: center;
    color: inherit;
    text-decoration: none;
    margin: auto;
    text-transform: uppercase;
    font-size: 0.8em;
    padding: 0.5em;
  }
  .header__menu .btn-menu {
    margin-left: 0.5rem;
  }
  .header__menu .btn-menu a {
    transition: all ease-out 0.4s;
    background-image: linear-gradient(90deg, var(--color-pink) 0%, var(--color-primary) 50%, var(--color-pink) 100%);
    background-size: 200% 100%;
    background-position: left bottom;
    border-radius: var(--radius-secondary);
    color: var(--color-white);
    padding: 0.8em 1.2em;
    text-decoration: none;
    font-weight: 500;
    display: inline-flex;
    box-shadow: 0 0 5rem transparent;
    border: 0;
    cursor: pointer;
  }
  .header__menu .btn-menu a:hover {
    background-position: right bottom;
    box-shadow: 0 0 5rem rgba(var(--color-pink--rgb), 0.7);
  }
  .header__menu__hover {
    transition: all cubic-bezier(0.18, 0.89, 0.32, 1.28) 0.3s;
    position: absolute;
    background-color: var(--color-primary);
    z-index: -1;
    height: calc(100% - 2 * var(--padding));
    border-radius: var(--radius-primary);
    opacity: 0.2;
  }
  .header__menu__hover.disable {
    opacity: 0;
  }
}
@media (width <= 900px) {
  .burger[aria-expanded=false] + .header__menu {
    top: -30%;
    opacity: 0;
    pointer-events: none;
  }
  .header__menu {
    transition: all ease 0.3s;
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 1;
    background-color: var(--color-primary);
    list-style-type: none;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  .header__menu a {
    transition: all ease 0.3s;
    font-size: clamp(5rem, 0.3rem + 12vw, 30rem);
    text-stroke: 1.5px var(--color-white);
    -webkit-text-stroke: 1.5px var(--color-white);
    color: transparent;
    text-transform: uppercase;
    font-weight: 600;
    line-height: 1.4;
    text-decoration: none;
  }
  .header__menu a:hover {
    color: var(--color-white);
  }
}
.skills {
  list-style-type: none;
  padding-bottom: 25%;
  display: grid;
  gap: var(--gap);
}
@media (width > 550px) {
  .skills {
    grid-template-columns: repeat(2, 1fr);
  }
}

.skill {
  border: var(--border);
  border-radius: var(--radius-primary);
  padding: max(3rem, 3vw);
  aspect-ratio: 1;
}
.skill__abbr {
  font-size: clamp(7rem, 0.3rem + 10vw, 30rem);
  text-stroke: 2px var(--color-primary);
  -webkit-text-stroke: 2px var(--color-primary);
  color: transparent;
  text-transform: uppercase;
  font-weight: 600;
  line-height: 1;
}
@supports (not (-webkit-text-stroke: 2px var(--color-primary))) and (not (text-stroke: 2px var(--color-primary))) {
  .skill__abbr {
    color: var(--color-primary);
  }
}
.skill__title {
  font-family: var(--font-secondary);
  font-size: var(--h2-font-size);
  font-weight: 600;
  line-height: 1.1em;
  margin-top: 0.4em;
}
.skill__content {
  font-size: 0.9em;
  margin-top: 0.7em;
}
.skill__list {
  list-style-type: none;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 1em;
}
.skill__list__single {
  text-transform: uppercase;
  font-size: 0.8em;
  font-weight: 500;
  display: contents;
}
.skill__list__single:not(:first-child)::before {
  content: "";
  display: block;
  width: 0.8rem;
  height: 0.8rem;
  margin-left: 1em;
  margin-right: 1em;
  background-color: var(--color-primary);
  border-radius: 100%;
}

.header-home {
  margin-top: clamp(5rem, 25vh, 30rem);
  margin-bottom: clamp(5rem, 25vh, 30rem);
}
.header-home h1 {
  font-size: var(--h1-font-size);
  text-transform: uppercase;
  font-weight: 600;
  line-height: 1;
}
.header-home h2 {
  font-size: var(--h2-font-size);
  font-family: var(--font-secondary);
  font-weight: 400;
  line-height: 1;
}
.header-home * + * {
  margin-top: 1rem;
}
.header-home .btn {
  margin-top: 3rem;
}

.folio {
  position: relative;
  overflow: hidden;
}
.folio a {
  display: block;
  text-decoration: none;
}
.folio__content {
  transition: color ease 0.4s;
  position: relative;
  z-index: 10;
  display: flex;
  align-items: center;
  color: var(--color-primary);
  padding-top: clamp(3rem, 2rem + 3vw, 10rem);
  padding-bottom: clamp(3rem, 2rem + 3vw, 10rem);
}
@media (width < 1000px) {
  .folio__content {
    flex-direction: column;
    align-items: flex-start;
  }
}
.folio__title {
  flex: 1;
  font-family: var(--font-secondary);
  font-weight: 300;
  font-size: clamp(3.2rem, 1rem + 4vw, 9rem);
  line-height: 1.2em;
}
.folio__thumbnail {
  transition: all ease-in-out 0.3s;
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 100%;
  z-index: 1;
}
.folio__thumbnail img {
  transition: all ease 0.4s;
  position: absolute;
  height: 100%;
  width: 100%;
  object-fit: cover;
  opacity: 0;
  filter: saturate(0);
  z-index: 2;
  mix-blend-mode: multiply;
}
.folio__thumbnail::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  background: var(--main-gradient);
  z-index: 1;
}
.folio__details {
  text-transform: uppercase;
}
@media (width >= 1000px) {
  .folio__details {
    text-align: right;
  }
}
.folio__details__skill {
  display: flex;
  justify-content: flex-end;
  list-style-type: none;
  font-size: 0.8em;
}
.folio__details__type {
  font-weight: 600;
}
.folio__details li + li::before {
  content: " - ";
}

.folio .folio__content {
  border-top: 2px solid var(--color-primary);
}

.folio:hover + .folio .folio__content {
  border-top-color: transparent;
}

.folio a:hover .folio__content {
  color: var(--color-white);
}
.folio a:hover .folio__thumbnail {
  top: 0;
}
.folio a:hover .folio__thumbnail img {
  opacity: 0.4;
}

.portfolio__header {
  align-items: center;
}
@media (width > 900px) {
  .portfolio__header {
    display: flex;
  }
}
.portfolio__header__title {
  line-height: 1.2em;
  text-transform: uppercase;
}
.portfolio__header__description {
  line-height: 1em;
  display: flex;
  align-items: center;
}
@media (width > 900px) {
  .portfolio__header__description::before {
    content: "";
    display: block;
    height: 0.8rem;
    width: 0.8rem;
    background-color: var(--color-primary);
    border-radius: 1rem;
    margin-left: 1em;
    margin-right: 1em;
  }
}

.folios {
  margin-top: var(--margin-fluid--tiny);
  list-style-type: none;
}

.cursor-wrapper {
  position: fixed;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  top: 0;
  z-index: 100;
  pointer-events: none;
}

.info-cursor {
  transition: all ease-out 0.6s;
  position: absolute;
  background-color: var(--color-white);
  border-radius: var(--radius-primary);
  display: inline-flex;
  align-items: center;
  font-size: 0.7em;
  padding: 0.5em 1.3em;
  box-shadow: 0 0 3rem rgba(0, 0, 0, 0.3);
  pointer-events: none;
  opacity: 0;
  flex-shrink: 0;
}
.info-cursor p {
  white-space: nowrap;
}
.info-cursor.show {
  opacity: 1;
}
.info-cursor svg {
  stroke: var(--color-primary);
  stroke-width: 2px;
  width: 1.8rem;
  margin-right: 0.8em;
  flex-shrink: 0;
}

.page-title {
  margin-top: var(--margin-fluid);
  margin-bottom: var(--margin-fluid);
  text-transform: uppercase;
}
.page-title h1, .page-title h2, .page-title h3, .page-title h4 {
  font-size: var(--h1-font-size);
  line-height: 1em;
  max-width: 90rem;
}

.wp-block-contact-form-7-contact-form-selector {
  margin-top: calc(var(--margin-fluid) / 2);
}
.wp-block-contact-form-7-contact-form-selector label {
  display: block;
  flex-direction: column;
  border: var(--border);
  border-radius: var(--radius-primary);
  padding: clamp(2rem, 2vw, 3.5rem);
  margin-bottom: clamp(2rem, 2vw, 5rem);
}
.wp-block-contact-form-7-contact-form-selector label span {
  display: block;
}
.wp-block-contact-form-7-contact-form-selector label span:last-child {
  text-transform: uppercase;
  font-size: 0.8em;
  font-weight: 600;
}
.wp-block-contact-form-7-contact-form-selector label input, .wp-block-contact-form-7-contact-form-selector label textarea {
  border: 0;
  background: none;
  color: inherit;
  outline: 0;
  width: 100%;
  font-size: 1.2em;
}
.wp-block-contact-form-7-contact-form-selector .wpcf7-not-valid-tip {
  margin-top: 1em;
}
.wp-block-contact-form-7-contact-form-selector .form-submit {
  --padding: .4rem;
  display: inline-flex;
  align-items: center;
  border: var(--border);
  border-radius: var(--radius-primary);
  padding: var(--padding);
}
.wp-block-contact-form-7-contact-form-selector .form-submit input {
  transition: all ease-out 0.4s;
  background-image: linear-gradient(90deg, var(--color-pink) 0%, var(--color-primary) 50%, var(--color-pink) 100%);
  background-size: 200% 100%;
  background-position: left bottom;
  border-radius: var(--radius-secondary);
  color: var(--color-white);
  padding: 0.8em 1.2em;
  text-decoration: none;
  font-weight: 500;
  display: inline-flex;
  box-shadow: 0 0 5rem transparent;
  border: 0;
  cursor: pointer;
}
.wp-block-contact-form-7-contact-form-selector .form-submit input:hover {
  background-position: right bottom;
  box-shadow: 0 0 5rem rgba(var(--color-pink--rgb), 0.7);
}
.wp-block-contact-form-7-contact-form-selector .form-submit .wpcf7-spinner {
  transition: margin ease 0.3s, opacity ease 0.2s;
  background: none;
  border: 2px solid var(--color-primary);
  width: 3rem;
  height: 3rem;
  opacity: 1;
}
.wp-block-contact-form-7-contact-form-selector .form-submit .wpcf7-spinner::before {
  background-color: var(--color-primary);
}

form:not(.submitting) .form-submit .wpcf7-spinner {
  margin: 0;
  width: 0;
  opacity: 0;
  border: 0;
}

.wpcf7 form .wpcf7-response-output {
  padding: 0;
  margin: 0;
  margin-top: clamp(2rem, 2vw, 5rem);
  border: var(--border);
  border-radius: var(--radius-primary);
  padding: clamp(2rem, 2vw, 5rem);
}

.wpcf7 form.sent .wpcf7-response-output {
  border-color: var(--color-green);
  color: var(--color-green);
}

.wpcf7 form.invalid .wpcf7-response-output {
  border-color: var(--color-primary);
  color: var(--color-primary);
}

.folio-galerie {
  padding: clamp(2rem, 5vw, 7rem);
  list-style-type: none;
  position: relative;
}
.folio-galerie::before {
  content: "";
  display: block;
  background-color: var(--color-brown);
  border-radius: var(--radius-primary);
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 70%;
  transform: translateY(-50%);
}
.folio-galerie__single {
  padding: 3.5rem 0.5rem 0.5rem;
  background-color: var(--color-white);
  border-radius: var(--radius-primary);
  position: relative;
  box-shadow: 0 0 7rem rgba(0, 0, 0, 0.14);
}
.folio-galerie__single img {
  border-radius: 0 0 var(--radius-secondary) var(--radius-secondary);
  width: 100%;
  height: auto;
}
.folio-galerie__single::before {
  --size: .8rem;
  --gap: 1.5;
  content: "";
  display: block;
  height: var(--size);
  width: var(--size);
  background-color: var(--color-brown);
  border-radius: 100%;
  position: absolute;
  top: 1.4rem;
  right: 2rem;
  box-shadow: calc(var(--size) * var(--gap) * -1) 0 0 var(--color-brown), calc(var(--size) * var(--gap) * -1 * 2) 0 0 var(--color-brown);
}

@media (width >= 950px) {
  .home .skills {
    margin-left: 30%;
  }
}
.page:not(.home) .skills {
  margin-top: 15%;
  padding-bottom: 15%;
}

.single-folio__details {
  list-style-type: none;
  margin-bottom: var(--margin-fluid);
}
.single-folio__details__skills__list {
  display: flex;
  list-style-type: none;
  align-items: center;
  text-transform: uppercase;
  font-size: 0.8em;
}
.single-folio__details__skills__list li:not(:first-child)::before {
  content: " - ";
}

.single-folio__details__type {
  text-transform: uppercase;
  font-weight: 600;
  font-size: 1.2em;
}

.single-folio__details__website {
  margin-top: 0.5em;
  text-transform: uppercase;
}
.single-folio__details__website a {
  transition: all ease 0.3s;
  color: inherit;
  text-decoration: none;
  font-weight: 500;
}
.single-folio__details__website a:hover {
  color: var(--color-pink);
}

.single-folio__content p + p {
  margin-top: 0.7em;
}

.single-folio__screenshot {
  margin-top: calc(var(--margin-fluid) * 2);
  margin-bottom: calc(var(--margin-fluid) * 2);
}

.error404 .global-wrapper {
  display: flex;
  flex-direction: column;
  min-height: calc(100vh - max(3vw, 2rem) * 2);
}

.p404__content {
  flex: 1;
  margin: auto;
  display: flex;
  position: relative;
}
.p404__content__wrapper {
  margin: auto;
  text-align: center;
}
.p404__content h1 {
  text-transform: uppercase;
  margin-bottom: 0.5em;
}
.p404__bg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  z-index: -1;
  opacity: 0.35;
}
.p404__bg span {
  font-size: clamp(12rem, 40vw, 50rem);
  text-stroke: 2px var(--color-primary);
  -webkit-text-stroke: 2px var(--color-primary);
  color: transparent;
  line-height: 1em;
  font-weight: 600;
}